.tb_cp_post_container {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.tb_cp_post_wrapper {
    width: 25%;
    padding: 7.5px;
}

.tb_cp_post_in {
    display: block;
    cursor: pointer;
    overflow: hidden;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.20);
    background-color: var(--tb-bg-color);
    border-radius: var(--tb-border-radius);
}

.tb_cp_image {
    margin: 0;
    background-repeat: no-repeat;
    background-size: contain !important;
    background-position: center;
}

.tb_cp_image_wrap_ {
    padding-bottom: 100%;
    position: relative;
}


.tb_cp_image, .tb_cp_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    margin: 0;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
}
.tb_cp_video{
    background-color: #000;
}

.tb_cp_video video,
.tb_cp_video iframe{
    object-fit: cover;
    object-position: center;
}

.tb_cp_video > div {
    display: flex;
    align-items: center;
    justify-content: center;
}


.tb_cp_author_wrapper {
    padding: 10px;
}

.tb_cp_media_wrap {
    position: relative;
}

.tb_cp_author {
    width: calc(100% - 30px);
}

.tb_cp_author_profile {
    height: 44px;
    width: 44px;
    border-radius: 50%;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    overflow: hidden;
    background-color: rgba(0, 0, 0, .2);
}

.tb_cp_author_profile__ {
    height: 100% !important;
    width: 100% !important;
    margin: 0;
    object-fit: cover;
    object-position: center;
}

.tb_cp_authorname {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 4px;
    white-space: nowrap;
    display: block;
    text-align: left;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: left !important;
    line-height: normal;
}

.tb_cp_authorname {
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
    font-style: var(--tb-font-style);
}

.tb_cp_username,
.tb_cp_time {
    font-family: var(--tb-font);
    color: var(--tb-author-color);
    font-weight: var(--tb-font-weight);
    font-style: var(--tb-font-style);
}

.tb_cp_seprator {
    background-color: var(--tb-author-color);
}

.tb_cp_content,
.tb_cp_rating_content {
    font-family: var(--tb-font);
    color: var(--tb-color);
    font-size: var(--tb-font-size);
    line-height: var(--tb-line-height);
}

.tb_cp_author {
    display: flex;
    align-items: center;
    flex-grow: 1;
}

.tb_cp_author_wrapper {
    display: flex;
    align-items: center;
}

.tb_cp_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: calc(100% - 54px);
    padding-right: 10px;
}

.tb_cp_post_info {
    display: flex;
    align-items: center;
}

.tb_cp_seprator {
    height: 4px;
    width: 4px;
    min-width: 4px;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.tb_cp_username,
.tb_cp_time {
    font-size: 10px;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_cp_username {
    max-width: 65%;
}

.tb_cp_time {
    max-width: 35%;
}

.tb_cp_social_ico{
    font-size: 20px;
    color: var(--tb-icon-color);
}

.tb_cp_post_in:hover .tb_share_wrapper {
    opacity: 1;
    visibility: visible;
}

.tb_cp_post_cta {
    padding: 0 10px;
}

.tb_cp_post_in:hover .tb__media_ico_ .tb__icon,
.tb_cp_post_in:hover .tb__media_ico_ .tb__icon{
    opacity: 0;
    transform: scaleX(.4) scaleY(.4) translateZ(0);
}

.tb_cp_youtube_ico {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 34px;
}

.tb_cp_social_actions_ {
    display: flex;
    background-color: rgba(0, 0, 0, .04);
    align-items: center;
    justify-content: end;
    flex-wrap: wrap;
}

.tb_cp_social_action_bg{
    background-color: rgba(0, 0, 0, 0.05);
}

.tb_cp_social_action_bg .tb_social_action__{
    border-top: none;
}

.tb_cp_action_counts_wrap {
    display: flex;
    padding: 3px;
    margin: 3px;
    align-items: center;
    cursor: pointer;
    text-decoration: none;
}

.tb_cp_action_counts {
    font-size: 12px;
    margin-left: 5px;
    font-weight: 600;
}

.tb_cp_action_ico {
    font-size: 14px;
}

.tb_cp_action_counts,
.tb_cp_action_ico {
    color: #fff;
    transition: all .3s ease-in-out;
}

.tb_cp_action_counts_wrap:hover .tb_cp_action_ico {
    transform: rotateY(180deg);
}

.tb_cp_post_cta {
    display: block;
    text-align: center;
    margin-top: 10px;
}

.tb_cp_post_cta_btn {
    font-family: var(--tb-font);
    padding: 12px;
    color: var(--tb-btn-color);
    background-color:  var(--tb-btn-bg-color);
    border-radius: var(--tb-btn-border-radius);
    font-weight: var(--tb-btn-font-weight);
    border-color: var(--tb-btn-border-color);
    transition: var(--tb-btn-transition);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    line-height: normal;
}

.tb_cp_post_cta_btn:hover {
    color: var(--tb-btn-color-hover);
    background-color: var(--tb-btn-bg-color-hover);
    border-color: var(--tb-btn-border-color-hover);
}